import React, { ReactElement } from "react";
import * as HouseAPI from "../api/houses";
import * as SchoolAPI from "../api/school";
import { House, HouseDTO } from "../api/houses/model.interface";
import { Grid } from "@material-ui/core";
import withPageLayout from "../hocs/withPageLayout";
import { PageHeaderContext } from "../context/layout";
import AppBtn from "../components/form/AppBtn";
import AppInput from "../components/form/AppInput";
import { withRouter } from "react-router";
import AppInputSelect from "../components/form/AppInputSelect";

const HouseAdd = (props: any) => {
  const LayoutContext = React.useContext(PageHeaderContext);

  const [schools, setSchools] = React.useState([]);

  const [name, setName] = React.useState("");
  const [schoolId, setSchoolId] = React.useState(0);
  const [colorCode, setColorCode] = React.useState("");

  const addHouse = async () => {
    props.setShowLoading(true);

    const houseDTO: HouseDTO = {
      name,
      colour_code: colorCode,
      school_id: schoolId,
    };
    const addedHouse = await HouseAPI.post(houseDTO);
    typeof props.history.goBack === "function" && props.history.goBack();
  };

  React.useEffect(() => {
    LayoutContext.setTitle("Add House");
    getData();
  }, []);

  const getData = async () => {
    props.setShowLoading(true);
    const schoolsList: any = await SchoolAPI.get();
    setSchools(schoolsList);
    props.setShowLoading(false);
  };
  return (
    <>
      <Grid>
        <AppInput
          label="Name"
          value={name}
          placeholder="Enter Name"
          prefix={null}
          suffix={null}
          onChangeText={(name: string) => setName(name)}
        />
        <AppInput
          label="Color"
          value={colorCode}
          type="color"
          placeholder="Color"
          prefix={null}
          suffix={null}
          onChangeText={(colorCode: string) => setColorCode(colorCode)}
        />

        <AppInputSelect
          value={schoolId}
          handleChange={(v: number) => setSchoolId(v)}
          data={schools}
          label="School"
        />

        <AppBtn block title="Save" onClick={addHouse} />
      </Grid>
    </>
  );
};
export default withRouter(withPageLayout(HouseAdd));
